import * as actionsTypes from "../actions/actionTypes";
import { updatedObject } from '../../shared/utility';

// Global Variable
const INGREDIENTS_PRICES = {
   salad: 0.5,
   cheese: 0.4, 
   meat: 1.3,
   bacon: 0.7
};

const initialState = {
   ingredients: null,
   totalPrice: 4,
   error:false,
   building: false
}

const addIngredient = (state, action)=>{
   const newAddIngredients = updatedObject(state.ingredients, { [action.ingredientName]: state.ingredients[action.ingredientName] + 1});
   return updatedObject(state, {
      ingredients: newAddIngredients,
      totalPrice: state.totalPrice + INGREDIENTS_PRICES[action.ingredientName],
      building: true
   });
}

const removeIngredient = (state, action) =>{
   const newRemIngredients = updatedObject(state.ingredients, { [action.ingredientName]: state.ingredients[action.ingredientName] - 1});
   return updatedObject(state, {
      ingredients: newRemIngredients,
      totalPrice: state.totalPrice - INGREDIENTS_PRICES[action.ingredientName],
      building: true
   });
}

const setIngredients = (state, action)=>{
   return updatedObject(state,{
      ingredients:{
         salad: action.ingredients.salad,
         bacon: action.ingredients.bacon,
         cheese: action.ingredients.cheese,
         meat: action.ingredients.meat
      },
      totalPrice: 4,
      error: false,
      building: false
   });
}

const fetchIngredientsFailed = (state, action)=>{
   return updatedObject(state, {error:true});
}

const reducer = (state = initialState, action) => {
   switch(action.type){
      case actionsTypes.ADD_INGREDIENT: return addIngredient(state, action);
      case actionsTypes.REMOVE_INGREDIENT: return removeIngredient(state,action);
      case actionsTypes.SET_INGREDIENTS: return setIngredients(state, action);
      case actionsTypes.FETCH_INGREDIENTS_FAILED: return fetchIngredientsFailed(state, action);
      default: return state;
   }
};

export default reducer;