import * as actionTypes from '../actions/actionTypes';
import { updatedObject } from '../../shared/utility';

const initialState = {
  orders: [],
  loading: false,
  purchased: false
};

const purchaseInit = (state, action) => {
  return updatedObject(state, {purchased: false});
};

const purchaseBurguerStart = (state, action) => {
  return updatedObject(state, {loading: true});
};

const purchaseBurguerSuccess = (state, action) =>{
  const newOrder = updatedObject(action.order,{id: action.orderId});
  return updatedObject(state,{
    loading: false, 
    purchased: true, 
    orders : state.orders.concat(newOrder)
  });
}

const purchaseBurguerFail = (state, action) =>{
  return updatedObject(state,{loading: false});
}

const fetchOrderStart = (state, action)=>{
  return updatedObject(state, {loading:true});
}

const fetchOrderSuccess = (state, action)=>{
  return updatedObject(state,{
    orders: action.orders,
    loading: false
  });
}

const fetchOrderFail = (state, action) =>{
  return updatedObject(state, {loading: false});
}
const reducer = (state = initialState, action) =>{
  switch(action.type){
    case actionTypes.PURCHASE_INIT: return purchaseInit(state, action);      
    case actionTypes.PURCHASE_BURGUER_START: return purchaseBurguerStart(state, action);
    case actionTypes.PURCHASE_BURGUER_SUCCESS: return purchaseBurguerSuccess(state, action);
    case actionTypes.PURCHASE_BURGUER_FAIL: return purchaseBurguerFail(state, action);
    case actionTypes.FETCH_ORDER_START: return fetchOrderStart(state, action);
    case actionTypes.FETCH_ORDER_SUCCESS: return fetchOrderSuccess(state, action);
    case actionTypes.FETCH_ORDER_FAIL: return fetchOrderFail(state, action);
    default: return state;
  }
};

export default reducer;