import React, { Component } from "react";

import Aux from "../../hoc/Auxiliar/Auxiliar"
import Burguer from "../../components/Burguer/Burguer";
import BuildControls from "../../components/Burguer/BuildControls/BuildControls";
import Modal from "../../components/UI/Modal/Modal"; 
import OrderSummary from "../../components/Burguer/OrderSummary/OrderSummary";
import withErrorHandler from "../../hoc/withErrorHandler/withErrorHandler";
import Spinner from "../../components/UI/Spinner/Spinner";
import {Redirect} from "react-router-dom";
import {connect} from "react-redux";

import * as actions from '../../store/actions';
import axios from "../../axio-orders";

export class BurguerBuilder extends Component{
   // Alternative to create State
   // constructor (props) {
   //    super(props);
   //    this.state = {
   //          Variveis aqui.
   //    }
   // }
   state ={
      purchasing: false,
      redirect: false
   }
   componentDidMount () {
      this.props.onInitIngredients();
   }
   updatePurchaseState(ingredients){ 
      const sum = Object.keys(ingredients)
      .map(igKey => {
         return ingredients[igKey];
      })
      .reduce(
         (sum,el)=>{
            return sum+el;          
         },
      0);
      return sum>0;
   }

   purchaseHandler = () => {
      if(this.props.isAuthenticated){
         this.setState({purchasing:true});
      } else {
         this.props.onSetAuthRedirectPath('/checkout');
         this.props.history.push('/auth');
      }
   }

   purchaseCancelHandler = () => {
      this.setState({purchasing:false});
   }

   purchaseContinueHandler  =()=>{
      this.props.history.push("/checkout");
   }
   render(){
      const disabledInfo ={
         ...this.props.ings
      };

      for(let key in disabledInfo){
         disabledInfo[key] = disabledInfo[key] <= 0;
      };

      let orderSummary = null;
      let burguer = this.props.error ? <p>Ingredients can't be loaded</p>:<Spinner />;

      if(this.props.ings){
         burguer = (
            <Aux>
               <Burguer ingredients = {this.props.ings}/>
               <BuildControls 
                  ingredientsRemoved={this.props.onIngredientRemoved}   
                  ingredientsAdded={this.props.onIngredientAdded}
                  disabled={disabledInfo}
                  purchaseable={this.updatePurchaseState(this.props.ings)}
                  isAuth={this.props.isAuthenticated}
                  price={this.props.price}
                  ordered = {this.purchaseHandler}
               />
            </Aux>
         );
         orderSummary = <OrderSummary 
            ingredients={this.props.ings}
            purchaseContinue = { this.purchaseContinueHandler  }
            purchaseCancel ={ this.purchaseCancelHandler }
            price={this.props.price}
         />; 
      }
      return(
         <Aux>
            {this.state.redirect ? <Redirect to="/checkout" />: null}
            <Modal show={this.state.purchasing} modalClosed={this.purchaseCancelHandler} >
               {orderSummary}
            </Modal>
            {burguer}
         </Aux>
      );
   }
}
const mapStateToProps = state =>{
   return{
      ings:state.burguerBuilder.ingredients,
      price:state.burguerBuilder.totalPrice,
      error: state.burguerBuilder.error,
      isAuthenticated: state.auth.token !== null
   };
}
const mapDispatchToProps = dispatch => {
   return {
      onIngredientAdded: (ingName) => dispatch(actions.addIngredient(ingName)),
      onIngredientRemoved: (ingName) => dispatch(actions.removeIngredient(ingName)),
      onInitIngredients: () => dispatch(actions.initIngredients()),
      onSetAuthRedirectPath: (path) => dispatch(actions.setAuthRedirectPath(path))
   }
}
export default connect(mapStateToProps, mapDispatchToProps)(withErrorHandler(BurguerBuilder, axios));