import React from "react";
import {withRouter} from "react-router-dom"

import classes from "./Burguer.module.css"
import BurguerIngredient from "./BurgerIngredient/BurgerIngredient";

const burguer = (props) => {
   let transformedIngredient = Object.keys(props.ingredients)
   .map( igKey => {
      return [...Array(props.ingredients[igKey])].map(
         (_,i) => {
            return <BurguerIngredient key={igKey+i} type={igKey} />
      });
   })
   .reduce( (prevArray, currentElement) => {
      return prevArray.concat(currentElement)
   },[]); 

   if(transformedIngredient.length===0){
      transformedIngredient = <p>Please start adding ingredients</p>
   }
   
   return(
      <div className={classes.Burguer}>
         <BurguerIngredient type="bread-top"/>
         {transformedIngredient}
         <BurguerIngredient type="bread-bottom"/>
      </div>
   );
}

export default withRouter(burguer);